  .textBold {
    font-weight: 900;
  }

  .textRed {
    color: rgb(252, 19, 19);
    font-weight: 900;
  }

  .bgGrey {
    background: #9a9a9a;
  }

  table {
    width: 100%;
    background: #9a9a9a;

    tbody {
      tr {
        td {
          padding: 0px 15px;
          font-size: 14px;
          line-height: 24px;

          .percent {
            float: right;
            background: rgb(49, 118, 247);
            color: #fff;
            padding: 2px 10px;
            box-sizing: border-box;
          }

          &:first-child {
            background: #bbbbbb;
            padding: 0;
          }

          &:nth-child(2) {
            text-align: center;
          }
        }

        .tdBadget{
          background: rgb(49, 118, 247);
          color: #fff;
          padding: 2px 10px;
          box-sizing: border-box;
        }

      }

      .m-10{
        td{
          padding: 10px 15px;
        }
      }
      .mt-10{
        td {
          padding-top: 10px !important;
        }
      }
      .mb-10{
        td{
          padding-bottom: 10px !important;
        }
      }
    }
  }
}
